.pageBody {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
.header {
  position: absolute;
  top: 0;
  height: 48px;
  width: 100%;
  background-color: #9ab6ca;
}
.backButton {
  background: url(../img/back_icon-96x96.png);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 48px;
  height: 48px;
  background-size: 48px;
  cursor: pointer;
}
.title {
  position: absolute;
  color: #ffffff;
  font-weight: bold;
  left: 48px;
  right: 48px;
  text-overflow: ellipsis;
  height: 100%;
  text-align: center;
  line-height: 48px;
}
.orderButton {
  display: none;
  background-color: #ff0000;
  position: absolute;
  font-weight: bold;
  right: 0;
  bottom: 0;
  width: 48px;
  height: 100%;
  cursor: pointer;
  font-size: 12px;
  word-break: break-all;
  padding: 3px;
}
.pages {
  position: absolute;
  width: 100%;
  top: 48px;
  bottom: 0;
}
