.projectPage {
  width: 100%;
  height: 100%;
}
.tabCtrl {
  position: relative;
  width: 100%;
  height: 100%;
}
.tabCtrlMenu {
  position: relative;
  background-color: #9ab6ca;
  top: 0;
  height: 48px;
  width: 100%;
  list-style: none;
  list-style-position: outside;
  margin: 0;
  padding: 0;
  cursor: pointer;
}
.tabCtrlMenu li {
  float: left;
  text-align: center;
  color: #999999;
  height: 100%;
  line-height: 48px;
  background-color: #f2f3f4;
}
.tabCtrlMenu li.active {
  background-color: #d9e0e4;
  font-weight: 700;
}
.tabCtrlContent {
  position: absolute;
  top: 48px;
  bottom: 0;
  left: 0;
  right: 0;
}
.tabCtrlPage {
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
  overflow: hidden;
}
.tabCtrlPage.active {
  display: block;
}
.viewerComponentContainer {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.tableComponentContainer {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.View3D,
.View2D,
.ViewAssistant {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  touch-action: none;
}

